<template>
  <div id='map' ref='myMap'>
    <div class="tools">
      <el-row>
        <el-col :span="6">
          <el-button type="primary" icon="el-icon-location" v-on:click.native="drawPoint" circle></el-button>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="6">
          <el-button type="success" icon="el-icon-check" v-on:click.native="searchPoi" circle></el-button>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="6">
          <el-button type="info" icon="el-icon-delete" v-on:click.native="clear" circle></el-button>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="6">
          <el-button type="warning" icon="el-icon-edit-outline" v-on:click.native="findBestPlace" circle></el-button>
        </el-col>
      </el-row>
    </div>
  </div>
</template>
<script>
import Map from 'ol/map'
import View from 'ol/view'
import TileLayer from 'ol/layer/tile'
import XYZ from 'ol/source/xyz'
import Vector from 'ol/layer/vector'
import Control from 'ol/control'
import Draw from 'ol/interaction/draw'
import VSource from 'ol/source/vector'
import Style from 'ol/style/style'
import Fill from 'ol/style/fill'
import Stroke from 'ol/style/stroke'
import Circle from 'ol/style/circle'
import 'ol/ol.css'
let globalMap, globalSource, globalDraw
export default {
  name: 'Map',
  mounted() {
    this.initMap()
  },
  data() {
    return {
      msg: 'Hello Map'
    }
  },
  methods: {
    initMap() {
      console.log(this.$refs.myMap)
      let source = (globalSource = new VSource())
      let vector = new Vector({
        source: source,
        style: new Style({
          fill: new Fill({
            color: 'rgba(255, 255, 255, 0.2)'
          }),
          stroke: new Stroke({
            color: '#ffcc33',
            width: 2
          }),
          image: new Circle({
            radius: 7,
            fill: new Fill({
              color: '#ffcc33'
            })
          })
        })
      })
      let markers = new Vector({
        source: source,
        style: new Style({
          fill: new Fill({
            color: 'rgba(255, 255, 255, 0.2)'
          }),
          stroke: new Stroke({
            color: '#ffcc33',
            width: 2
          }),
          image: new Circle({
            radius: 7,
            fill: new Fill({
              color: '#ffcc33'
            })
          })
        })
      })
      let map = (globalMap = new Map({
        layers: [
          new TileLayer({
            source: new XYZ({
              url:
                'http://t2.supermapcloud.com/FileService/image?map=quanguo&type=web&x={x}&y={y}&z={z}'
            })
          }),
          vector,
          markers
        ],
        target: this.$refs.myMap,
        controls: Control.defaults({
          attributionOptions: {
            collapsible: false
          }
        }),
        view: new View({
          center: [12685153.492296234, 2577384.2465164554],
          zoom: 12
        })
      }))
      map.on('moveend', () => {
        let view = map.getView()
        console.log(view.getCenter())
        console.log(view.getZoom())
      })
    },
    drawPoint() {
      if (globalDraw) {
        globalDraw.setActive(true)
        return
      }
      globalDraw = new Draw({
        source: globalSource,
        type: 'Point'
      })
      globalMap.addInteraction(globalDraw)
    },
    searchPoi() {
      globalDraw.setActive(false)
      let features = globalSource.getFeatures()
      var points = []
      for (let i = 0, len = features.length; i < len; i++) {
        let geometry = features[i]
          .getGeometry()
          .transform('EPSG:3857', 'EPSG:4326')
        points.push(
          geometry
            .getCoordinates()
            .map(data => {
              return data.toFixed(6)
            })
            .join(',')
        )
      }
      let geometry = features[0].getGeometry()
      points.push(
        geometry
          .getCoordinates()
          .map(data => {
            return data.toFixed(6)
          })
          .join(',')
      )
      points = points.join('|')
      let url = `http://restapi.amap.com/v3/place/polygon?key=c673e715513d38b5dd75564d8ec4f717&types=050000&polygon=${points}&keywords=美食&output=json`
      fetch(url).then(data => {
        console.log(data)
      })
    },
    clear() {
      globalDraw.setActive(false)
      globalSource.clear()
    },
    findBestPlace() {
      globalDraw.setActive(false)
    }
  }
}
</script>
<style scoped>
#map {
  width: 100%;
}
.el-row {
  line-height: 60px;
}
.tools {
  position: absolute;
  top: 8.5em;
  left: 0.5em;
  z-index: 999;
}
</style>



